"use client";

import Link from "next/link";
import { useSidebar } from "@/context/sidebarContext";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  ChevronLeft,
  LayoutDashboard,
  Bell,
  Shield,
  FileText,
  Settings,
  Users,
  Server,
  Activity
} from "lucide-react";

const navigation = [
  { name: "Dashboard", href: "/", icon: LayoutDashboard },
  { name: "Security Events", href: "/events", icon: Activity },
  { name: "Alerts", href: "/alerts", icon: Bell },
  { name: "Agents", href: "/agents", icon: Users },
  { name: "Vulnerabilities", href: "/vulnerabilities", icon: Shield },
  { name: "Reports", href: "/reports", icon: FileText },
  { name: "Inventory", href: "/inventory", icon: Server },
  { name: "Settings", href: "/settings", icon: Settings },
];

export default function Sidebar() {
  const { isSidebarCollapsed, toggleSidebar } = useSidebar();



  return (
    <aside
      className={cn(
        "fixed left-0 top-0 z-40 h-screen bg-gray-900 text-white transition-all duration-300",
        isSidebarCollapsed ? "w-16" : "w-64"
      )}
    >
      <div className="flex h-full flex-col">
        <div className="flex h-16 items-center justify-between px-4">
          {!isSidebarCollapsed && (
            <span className="text-xl font-bold">Wazuh</span>
          )}
          <Button
            variant="ghost"
            size="icon"
            className="ml-auto text-white hover:bg-gray-800"
            onClick={toggleSidebar}
          >
            <ChevronLeft
              className={cn(
                "h-4 w-4 transition-transform",
                isSidebarCollapsed && "rotate-180"
              )}
            />
          </Button>
        </div>

        <nav className="flex-1 space-y-1 px-2 py-4">
          {navigation.map((item) => {
            const Icon = item.icon;
            return (
              <Link
                key={item.name}
                href={item.href}
                className={cn(
                  "flex items-center rounded-lg px-3 py-2 text-sm font-medium hover:bg-gray-800",
                  "transition-colors duration-200"
                )}
              >
                <Icon className={cn("h-5 w-5", isSidebarCollapsed ? "mx-auto" : "mr-3")} />
                {!isSidebarCollapsed && <span>{item.name}</span>}
              </Link>
            );
          })}
        </nav>
      </div>
    </aside>
  );
}
